<template>
  <div>
    <b-jumbotron header="Estimating Areas with Finite Sums" lead="By John Panos as a Vue.js, D3.js, and MathJax app">
    </b-jumbotron>
    <b-container>
    <b-card>
      <b-media no-body>
        <b-media-body class="ml-3">
          <h5 class="mt-0">Introduction</h5>
          <p>
            Estimating Areas with Finite sums is approximating the area of a curve by splitting the graph into chunks, usually rectangles, and take the area of each individual shape and add it together. The more shapes you have under the curve, the more accurate the esimation becomes.
          </p>
          <h5 class="mt-0">Applied Context</h5>
          <p>By taking the area under the curve, you can get distance traveled, or you can get position from a speed graph if you know the starting conditions.</p>
          <h5 class="mt-0">Common Errors/Mistakes</h5>
          <p>Common errors can include adding incorrectly and just simple math errors during the process. Other errors can include using Right Riemann Sum instead of Left or Mid and vice-versa.</p>
        </b-media-body>
      </b-media>
    </b-card>
    <br/>

    <h1>How to estimate area under a curve</h1>
    <b-card-group deck>
      <b-card>
        <b-card-title>1. Width of a rectangle</b-card-title>
        
        <b-card-text>The width of a single rectangle is the end of the range, minus the start of the range, divided by the number of rectangles you'd like. Essentially just splitting the range into n pieces, which the pieces being the width of the rectangle.</b-card-text>
        <vue-mathjax formula="$$w = \Delta x = {(b-a)\over n}$$"></vue-mathjax>
      </b-card>

      <b-card title="2. Height of a rectangle" header-tag="header" footer-tag="footer">
        <b-card-text>The height of the rectangle will be the height of (f(x_i)).  (x_i) is equal to the start of the range (a) plus (i) which i = nth rectangle times (\Delta x), with (\Delta x) being established as the width of the rectangle.</b-card-text>
        <vue-mathjax formula="Right Point Sum: $x_i = {a+i\Delta x}$"></vue-mathjax><br/>
        <vue-mathjax formula="Left Point Sum: $x_i = {x_{i-1}}$"></vue-mathjax><br/>
        <vue-mathjax formula="Mid Point Sum: $x_i = {x_i + {x_{i-1}} \over 2}$"></vue-mathjax>
        <vue-mathjax formula="$$h = {f(x_i)}$$"></vue-mathjax>
      </b-card>
      <b-card>
        <b-card-title>3. Area of the ith rectangle</b-card-title>
        <b-card-text>So now that we've defined the height as (h = f(x_i)) and the width as (\Delta x = {(b-a)\over n}) then that means we can calculate the area of one rectangle in the series!</b-card-text>
        <vue-mathjax formula="$$A = {\Delta x f(a+i\Delta x)}$$"></vue-mathjax>
      </b-card>
      <b-card>
        <b-card-title>4. Total area of all rectangles under a curve</b-card-title>
        <b-card-text>Now that we know how to take the area of an ith rectangle, then what if we take the area of all the ith rectangles and add them together. This would supply us with an estimate of the area under the curve.</b-card-text>
        <vue-mathjax formula="$$\displaystyle \sum_{i=1}^n {\Delta x f(a+i\Delta x)}$$"></vue-mathjax>
      </b-card>
    </b-card-group>
    <b-container>
      <br/>
    <h1>Example 1</h1>
      <vue-mathjax formula="$$f(x) = x^2, a = -5, b = 5$$"></vue-mathjax><br/>
      <vue-mathjax formula="$$\Delta x = {-5-5 \over 10} = -1$$"></vue-mathjax>
      <vue-mathjax formula="$$x_i = {-5+i\Delta x}$$"></vue-mathjax>
    </b-container>
    <b-card-group>
      <b-card>
        <img src="./assets/leftsum*2.svg" width="100%"/>
        <b-card-title>Left Riemann Sum</b-card-title>
        <b-card-text>The Left Riemann Sum is an overestimate when the slope of (f(x)) is negative, and an underestimate when the slope is positive.</b-card-text>
        <vue-mathjax formula="$$x_i = {-5+i(-1)}-1$$"></vue-mathjax>
        <vue-mathjax formula="$$A = 25 + 16 + 9 + 4 + 1 + 1 + 4 + 9 + 16 = 85$$"></vue-mathjax>
      </b-card>
      <b-card>
        <img src="./assets/midpointsum*2.svg" width="100%"/>
        <b-card-title>Midpoint Riemann Sum</b-card-title>
        <b-card-text>The Midpoint Riemann Sum is an overestimate when (f(x)) is concave down, and an underestimate when (f(x)) is concave up.</b-card-text>
        <vue-mathjax formula="$$A = 20.25 + 12.25 + 6.25 + 2.25 + 0.25 + 0.25 + 2.25 + 6.25 + 12.25 + 20.25 = 82.5$$"></vue-mathjax>
      </b-card>
      <b-card>
        <img src="./assets/rightsum*2.svg" width="100%"/>
        <b-card-title>Right Riemann Sum</b-card-title>
        <b-card-text>The Right Riemann Sum is an underestimate when the slope of (f(x)) is negative, and an overestimate when the slope is positive.</b-card-text>
        <vue-mathjax formula="$$x_i = {-5+i(-1)}$$"></vue-mathjax>
        <vue-mathjax formula="$$A = 16 + 9 + 4 + 1 + 0 + 1 + 4 + 9 + 16 + 25 = 106.3$$"></vue-mathjax>
      </b-card>
    </b-card-group>
    <br/>
    <h1>Example 2</h1>
    <vue-mathjax formula="$$f(x) = {1 \over 1 + x^2}, a = 0, b = 10$$"></vue-mathjax>
    <vue-mathjax formula="$$\Delta x = {10-0 \over 10} = 1$$"></vue-mathjax>
    <vue-mathjax formula="$$x_i = {0+i\Delta x-1}$$"></vue-mathjax>
    <b-card-group>
      <b-card>
        <img src="./assets/leftsum.svg" width="100%"/>
        <b-card-title>Left Riemann Sum</b-card-title>
        <vue-mathjax formula="$$A = 1(1.0 + 0.5 + 0.2 + 0.1 + 0.05882353 + 0.03846154 + 0.02702703 + 0.02 + 0.01538462 + 0.01219512)=1.9719$$"></vue-mathjax>
      </b-card>
      <b-card>
        <img src="./assets/midpointsum.svg" width="100%"/>
        <b-card-title>Midpoint Riemann Sum</b-card-title>
        <vue-mathjax formula="$$A = 1(0.8 + 0.30769231 + 0.13793103 + 0.0754717 + 0.04705882 + 0.032 + 0.02312139 + 0.01746725 + 0.01365188 + 0.0109589)=1.5$$"></vue-mathjax>
      </b-card>
      <b-card>
        <img src="./assets/rightsum.svg" width="100%"/>
        <b-card-title>Right Riemann Sum</b-card-title>
        <vue-mathjax formula="$$A = 1(0.5 + 0.2 + 0.1 + 0.05882353 + 0.03846154 + 0.02702703 + 0.02 + 0.01538462 + 0.01219512 + 0.00990099)=0.98$$"></vue-mathjax>
        
      </b-card>
    </b-card-group>
    </b-container>
  </div>
</template>

